import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

/**
 * Generated class for the InfiniteScrollPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-infinite-scroll',
  templateUrl: 'infinite-scroll.html',
})
export class InfiniteScrollPage {
  data;
  itemsToDisplay =[];
  item;

  constructor(public navCtrl: NavController, public navParams: NavParams) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad InfiniteScrollPage');
     this.itemsToDisplay = Array(450).fill(0).map((e,i)=>i);
      //  for(let i=0; i< 50; i++) {
      //   this.itemsToDisplay.push(this.data[i]);
      //  }
  }

  doInfinite(e){
    console.log(e)
    let len = this.itemsToDisplay.length;
    setTimeout(() => {
      console.log('Done');
      
      //loading some data from server
    
      e.state = "closed";
      if (this.itemsToDisplay.length > 100) {
        e.target.disabled = true;
      }
    }, 500);
  }
}



